<template>
    <div class="mini-chart">
    <Line
        :chart-options="chartOptions"
        :chart-data="allChartData"
        :chart-id="chartId"
        :dataset-id-key="datasetIdKey"
        :plugins="plugins"
        :css-classes="cssClasses"
        :styles="styles"
        :width="width"
        :height="height"
    />
    </div>
</template>

<script>
import { reactive, computed } from 'vue'
import { defineComponent } from '@vue/composition-api'
import { Line } from 'vue-chartjs'
import { Chart as ChartJS, Title,/* Tooltip,*/ Legend, LineElement, CategoryScale, LinearScale, PointElement } from 'chart.js'

ChartJS.register(
  Title,
//   Tooltip,
  Legend,
  LineElement,
  LinearScale,
  PointElement,
  CategoryScale,
)

export default defineComponent({
      components: { Line },
      props: {
        chartId: {
            type: String,
            default: 'line-chart'
        },
        chartData: {
            type: Array,
            default: () => []
        },
        datasetIdKey: {
            type: String,
            default: 'label'
        },
        width: {
            type: Number,
            default: 170
        },
        height: {
            type: Number,
            default: 30
        },
        cssClasses: {
            default: '',
            type: String
        },
        styles: {
            type: Object,
            default: () => {
                return {}
                // return { backgroundColor: '#28BEB2' }
            }
        },
        plugins: {
            type: Object,
            default: () => {}
        }
    },
    computed: {
          allChartData() {
              return {
                labels: this.chartData.map(x=>''),
                datasets: [ { data: this.chartData,
                borderColor: '#28BEB2',
                borderWidth: 2,
                backgroundColor: '#28BEB2',
                pointRadius: 0,
                pointHoverRadius: 0,
                pointHitRadius: 0
                } ]
            }
        },
    },
    setup(props) {
        let reactiveChartData = reactive(props.chartData);

        const chartOptions = reactive({
            responsive: false,
            scales: {
                y: {
                    min: computed(()=>{
                        let min = Math.min(...reactiveChartData.map(p=>p.y));
                        let max = Math.max(...reactiveChartData.map(p=>p.y));
                        let a = reactiveChartData[0].y;
                        return a - Math.max(Math.abs(a - min), Math.abs(a - max)) - (min == max ? 1 : 0);
                    }),
                    max: computed(()=>{
                        let min = Math.min(...reactiveChartData.map(p=>p.y));
                        let max = Math.max(...reactiveChartData.map(p=>p.y));
                        let a = reactiveChartData[0].y;
                        return a + Math.max(Math.abs(a - min), Math.abs(a - max)) + (min == max ? 1 : 0);
                    }),
                    display: false,
                    title: {
                        display: false,
                    },
                    ticks: {
                        display: false,
                        max: computed(()=>Math.max(...reactiveChartData.map(p=>p.y)))
                    },
                    grid: {
                        display: false
                    },
                },
                x: {
                    display: false,
                    title: {
                        display: false,
                    },
                    ticks: {
                        display: false
                    },
                    grid: {
                        display: false
                    },
                },
            },
            plugins: {
                legend: {
                    display: false,
                },
            }

        })
        return {
            chartOptions
        }
    },
})
</script>

